'use client';

import React, { useEffect } from 'react';
import Script from 'next/script';

// This is required to make TypeScript and React accept the custom element
declare global {
  namespace JSX {
    interface IntrinsicElements {
      'vturb-smartplayer': React.DetailedHTMLProps<React.HTMLAttributes<HTMLElement>, HTMLElement> & {
        id: string;
        'cta-btn-text': string;
        'cta-btn-play-video': string;
      };
    }
  }
}


interface VturbPlayerProps {
  playerId: string;
  playerSrc: string;
}

const VturbPlayer: React.FC<VturbPlayerProps> = ({ playerId, playerSrc }) => {
  
  return (
    <>
      <Script src={playerSrc} strategy="lazyOnload" />
      <vturb-smartplayer 
          id={playerId} 
          cta-btn-text="PLAY"
          cta-btn-play-video="true"
          style={{ display: 'block', margin: '0 auto', width: '100%', maxWidth: '853px' }}
      ></vturb-smartplayer>
    </>
  );
};

export default VturbPlayer;
