const CtaButton: FC<{ children: ReactNode; className?: string; asChild?: boolean; href?: string }> = ({ children, className, asChild, href }) => {
  const Comp = asChild ? 'a' : Button;
  const props = asChild ? { href } : {};

  // Check if children is a string and contains "Quero Acesso Imediato"
  let isCta = false;
  if (typeof children === 'string' && children.includes('Quero Acesso Imediato')) {
    isCta = true;
  } else if (React.isValidElement(children) && typeof children.props.children === 'string' && children.props.children.includes('Quero Acesso Imediato')) {
    isCta = true;
  } else if (Array.isArray(children)) {
    isCta = children.some(child => typeof child === 'string' && child.includes('Quero Acesso Imediato'));
  }
  
  const textContent = React.Children.toArray(children).find(child => typeof child === 'string');

  const hasCtaText = React.Children.toArray(children).some(child => {
    if (typeof child === 'string') {
        return child.toLowerCase().includes('quero acesso imediato');
    }
    // Also check for nested children like in the FAQ button
    if (React.isValidElement(child) && child.props.children) {
        return React.Children.toArray(child.props.children).some(c => typeof c === 'string' && c.toLowerCase().includes('quero acesso imediato'));
    }
    return false;
  });

  // A bit more robust check
  let finalChildren = children;
  let isImmediateAccess = false;

  React.Children.forEach(children, child => {
    if (typeof child === 'string' && child.toLowerCase().includes('quero acesso imediato')) {
      isImmediateAccess = true;
    }
    if (React.isValidElement(child) && child.props && typeof child.props.children === 'string') {
        if(child.props.children.toLowerCase().includes('quero acesso imediato')) {
            isImmediateAccess = true;
        }
    }
  });


  return (
    <Comp
      {...props}
      className={cn(
        "text-white font-bold text-lg py-6 px-10 rounded-lg shadow-lg hover:shadow-xl transform hover:-translate-y-1 transition-all duration-300 ease-in-out",
        isImmediateAccess ? "bg-[#3fb737] hover:bg-[#35a630]" : "bg-gradient-to-r from-[#FF6F00] to-[#F44336]",
        className
      )}
    >
      {children}
    </Comp>
  );
};

